import {Select} from '../components/select';
function selectSettings(){
   if (document.querySelector('.search-parameters')){
      new Select('#select-maker', {
         placeholder: '',
         data: [
            {id: '1', value: '1'},
            {id: '2', value: '2'},
            {id: '3', value: '4'}
         ]
      });
      new Select('#select-model', {
         placeholder: '',
         data: [
            {id: '1', value: '1'},
            {id: '2', value: '2'},
            {id: '3', value: '3'}
         ],
         disabled: false
      });
      new Select('#select-generation', {
         placeholder: '',
         data: [
            {id: '1', value: '1'},
            {id: '2', value: '2'},
            {id: '3', value: '3'}
         ],
         disabled: true
      });
      new Select('#select-modification', {
         placeholder: '',
         data: [
            {id: '1', value: '1'},
            {id: '2', value: '2'},
            {id: '3', value: '3'}
         ],
         disabled: true
      });
      new Select('#select-car-body', {
         placeholder: '',
         data: [
            {id: '1', value: '1'},
            {id: '2', value: '2'},
            {id: '3', value: '3'}
         ],
         disabled: true
         // selectedId: '3'
      });
   }

   if (document.querySelector('.catalog-content')){
      new Select('#select-width', {
         placeholder: '',
         data: [
            {id: '1', value: ''},
            {id: '2', value: '2'},
            {id: '3', value: '4'}
         ]
      });
      new Select('#select-profile', {
         placeholder: '',
         data: [
            {id: '1', value: ''},
            {id: '2', value: '2'},
            {id: '3', value: '4'}
         ]
      });
      new Select('#select-diameter', {
         placeholder: '',
         data: [
            {id: '1', value: ''},
            {id: '2', value: '2'},
            {id: '3', value: '4'}
         ]
      });
      new Select('#select-sort', {
         placeholder: ' ',
         data: [
            {id: '1', value: ' '},
            {id: '2', value: ' '},
            {id: '3', value: ''}
         ]
      });
   }
   
}
export default selectSettings;